#calc {
    box-shadow: 0 0.1px 4px 0 #04090a, 0 0.5px 2px 0 black;
    min-width: 217px;
    max-width: 217px;
    margin: auto;
    margin-top: 100px;

    padding: 20px;
    padding-left: 28px;
    background-color: #bdb7b7;
}

#calc input {
    padding: 7px;
    padding-top: 10px;
    font-size: 16px;
    min-width: 188px;
    max-width: 188px;
    margin-bottom: 10px;
    background-color: #b4e3f3;

}

#calc button {
    box-shadow: 0 1px 5px 0 #234042;
    min-width: 43px;
    margin-top: 11px;
    margin-right: 11px;
    padding: 12px;
    border: none;
    color: #ffffff;
    background-color: #000000;
    transition-duration: 0.5s;
    font-size: 15px;
    cursor: pointer;
}

#calc button:active {
    transform: scale(85%);
}

#calc #symbol {
    color: white;
    min-width: 43px;
    margin-top: 11px;
    margin-right: 11px;
    padding: 9.6px;
    font-size: large;
    background-color: #00008b;
    transition-duration: 0.5s;
}

#calc #lastbuttons {
    min-width: 97px;
    margin-bottom: 2px;
}

#calc .clear {
    background-color: #920002;
}

#calc form {
    display: inline;
}

#calc form button {
    background-color: #086808;
}