.login * {
    padding: 0%;
    margin: 0%;
    box-sizing: border-box;
}


.login {
    font-size: 18px;
    padding: 15px;
    margin: auto;
    min-width: 270px;
    max-width: 270px;
    margin-top: 100px;
    margin-bottom: 50px;
    box-shadow: 1px 1.5px 4px 1px #000000;
    padding: 8px;
    font-size: 120%;
    color: #ececec;
    font-size:120%;
    /* background-color: #9cd2ff; */
    /* background-color: #920002; */

    background-color: rgba(255, 255, 255, 0.24);

    /* background: linear-gradient(45deg, rgba(0,0,194,1) 0%, rgba(209,0,3,1) 0%, rgba(146,0,2,0.85) 99%, rgba(146,0,2,0.85) 100%);    */
    /* background: linear-gradient(to right, rgba(179, 220, 237, 1) 0%, rgba(41, 184, 229, 1) 100%); */
}

.login label{
    font-size: 109%;
    padding-left: 10px;
}

.login small{
    
    padding-left: 10px;
    margin-bottom: 20px;
}

.login input {
    padding: 3px;
    margin: 10.5px;
    width: 92%;
    border: 1.5px solid #000000;
    background-color: #ececec;
    border-radius: 7px;
    color: #000000;
    font: 70% sans-serif;
}

.login button {
    display: inline;
    padding: 5px;
    padding-left: 7px;
    padding-right: 7px;
    margin-top: 10px;
    margin-bottom: 8px;
    border-radius: 2.5px;
    font-size: 15px;
    border: none;
    background-color: #ececec;
    color: #000000;
    box-shadow: 0 0.5px 3px 0 #000000;
    transition-duration: 0.5s;
}

.login button:hover {
    color: #ffffff;
    background-color: #000000;
}

 


