#signupform {
    background-color: #fffcfc;

    padding: 0%;
    border-radius: 6px;
    margin: auto;
    margin-top: 100px;
    min-width: 300px;
    max-width: 350px;
    box-shadow: 0 0.5px 4px 0 #000000;
    padding: 7px;
    font-size: 120%;
    color: black;
    background-color: #faaab3;
    margin-bottom: 12px;
}

#signupform input {
    border: none;
    box-shadow: 0 0.5px 3.2px 0 #000000;
    background-color: #fdf0f1;
    border-radius: 7px;
    color: black;
    font: 70% sans-serif;
    width: 93%;
    margin: 5px;
    margin-left: 8px;
    margin-bottom: 15px;
    padding: 5px;
}

#signupform button {
    display: inline-block;
    margin: auto;
    border: none;
    font-size: 95%;
    padding: 5px;
    padding-top: 6px;
    min-width: 100px;
    border-radius: 4px;
    background-color: #faaab3;
    color: black;
    box-shadow: 0 0.5px 3.5px 0 black;
    margin-top: 20px;
    transition-duration: 0.5s;
    margin-bottom: 10px;
}

#signupform button:hover {
    color: #F7DADD;
    background-color: black;
}


#signupform label {
    padding-left: 6px;
    text-transform: capitalize;
    text-decoration: dotted;
}
