nav {
    background-color: #920002;
    box-shadow: 0 0.2px 3px 0 black;
    font-size: large;
    position: sticky;
    top: 0;
    /* opacity: 0%; */
}

nav li:hover {
    /* border: 1.5px white solid; */
    transition: 0.5s;
    background-color: black;




}

nav ul {
    margin: 0px;
}

nav li {
    display: inline-block;
}

nav li a {
    color: #c7c7c7;
    padding: 16.5px;
    display: inline-block;
    text-decoration: none;
}

